@import "@assets/styles/colors";

.userListMainContainer {
  padding: 15px;

  .usersicone {
    width: 100%;
    min-height: 100px;
    margin: 20px 0px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 10px;

    @media screen and (max-width: 420px) {
      justify-content: center;
      div {
        width: 100%;
      }
    }

    .buttonContainerClientDetails {
      padding-bottom: 10px;
      display: flex;
      flex-wrap: wrap;
      gap: 5px;

      @media screen and (max-width: 420px) {
        display: flex;
        flex-direction: column;
        width: 100%;

        button {
          width: 80% !important;
          margin: auto;
        }
      }
    }

    .UseBtns {
      display: flex;
      flex-direction: row;
      justify-content: end;
      align-items: center;
      flex-wrap: wrap;

      .btnContainers {
        display: flex;
        flex-wrap: wrap;
      }
    }
  }

  .userListContainer {
    background-color: var(--pagesContainerFluidBg);
    box-shadow: var(--pagesContainerFluidBoxShadow);
    border: var(--pagesContainerFluidBorder);
    width: 100%;
    height: auto;
    border-radius: 15px;

    .headerBtnCard {
      background-color: var(--cardBg);
      padding: 10px 0px;
      border: var(--cardBorder);
    }

    .ClickBtndiv { 
      height: 50px;
      margin: auto;
      background-color: var(--titleBarBg);
      display: flex;
      flex-direction: row;
      align-items: center;
      padding-left: 20px;

      .loadBtn {
        background-color: #ffc107;
        border:1px solid #ffc107;
        div {
          color: black;
        }
      }
    }

    .titleBarBorder {
      border-radius: 15px 15px 0px 0px; 
      border: var(--cardBorder);
    }

    .titleBarBorder_2 {
      border-radius: 0px;
      margin-top: 20px;
      border: var(--cardBorder);
    }

    .searchBarButtunCard {
      background-color: var(--cardBg);
      margin-bottom: 20px;
      border: var(--cardBorder);

      .searchBarCol {
        display: flex;

        .searchBarButton {
          margin: 20px;
          align-items: inside;

          :global(.ant-input-search-button) {
            background-color: var(--filledBtnBg);
          }
        }
      }
    }

    .clientAndTableCard {
      background-color: var(--cardBg);
      border: var(--cardBorder);
      padding-bottom: 20px;
      padding: 0px 20px;

      .titleBarClientsList { 
        border-radius: 0px;
        border: var(--cardBorder);
      }

      .BtnList {
        background-color: var(--outlinedBtnBg);
        color: var(--fontColor);
        position: relative;
        border: 0;
        padding: 12px;
        word-spacing: normal;

        img {
          margin-right: 5px;
        }
      }
      .BtnList:hover {
        color: var(--fontColor) !important;
        border: 0;
      }

      .userIcons {
        display: flex;
        flex-direction: row;
        justify-content: end;
        margin: 20px 15px 0px 0px;
        padding: 0px;
        background-color: var(--fontColor);
        height: 20px;
        border: 0;

        &:hover {
          color: var(--pagesBg);
          border: 0;
        }
      }
    }
  }
}
