.mainCricketWrraper {

    .mainHeader {
        color: var(--fontColorWithBg);
        background-color: var(--titleBarBg);
        width: 100%;
        margin: 6px;

        .detailsWrraper {
            display: flex;

            .headerImgWrraper {
                padding: 4px;

                img {
                    background-color: #00b181;
                    padding: 10px;
                    height: 48px;
                    width: 48px;
                    border-radius: 3px;
                }
            }

            .timeDetailsWrraper {
                display: flex;
                flex-direction: column;

                .tabsWrraper {
                    :global(.ant-tabs-nav) {
                        margin: 0px;
                    }
                    :global(.ant-tabs-nav-list) {
                        width: 100%;
                        flex-direction: row;
                        justify-content: space-around;



                        :global(.ant-tabs-tab) {
                            width: 46%;
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            margin: 0px;
                            border-radius: 8px;
                            background-color: #00b181;
                            border: 1px solid #00b181;
                            border-top-left-radius: 16px;
                            border-top-right-radius: 16px;
                            color: var(--fontColorWithBg);
                            margin-left: 8px;
                            padding: 6px 4px;

                            &:hover {
                                background-color: var(--sideBarSelectedTabBg);
                                color: var(--fontColorWithBg);
                            }
                        }

                        :global(.ant-tabs-tab-active) {
                            background-color: var(--sideBarSelectedTabBg);
                            border-radius: 8px;
                            border-top-left-radius: 16px;
                            border-top-right-radius: 16px;
                        }

                        :global(.ant-tabs-tab-btn) {
                            // color: var(--fontColorWithBg);
                            color: #fff;
                            padding: 0px 20px;
                        }

                        :global(.ant-tabs-ink-bar-animated) {
                            display: none;
                        }


                    }
                }

                .timeDetilSection {
                    display: flex;

                    img {
                        height: 20px;
                        width: 20px;
                        margin-top: 8px;
                        margin-left: 4px;
                    }

                    p {
                        margin: 0px;
                        margin-top: 8px;
                        margin-left: 4px;
                    }
                }


                .matchLabel {
                    margin-left: 6px;
                    margin-top: 6px;
                }
            }

            .countriesWrraper {
                margin-top: 5px;

                span {
                    margin-left: 6px;
                    font-size: 24px;
                    font-weight: 700;
                }

                @media screen and (max-width: 990px) {
                    span {
                        font-size: 18px;
                    }
                }
            }
        }

        .statusWrraper {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            span {
                padding: 8px;
                color: #00b181;
                font-size: 25px;
                font-weight: 600;
                text-transform: uppercase;
                margin-right: 10px;
            }
        }

        @media screen and (max-width: 990px) {
            .statusWrraper {
                justify-content: flex-start;
            }
        }

        @media screen and (max-width: 630px) {
            .statusWrraper {
                span {
                    font-size: 20px;
                }
            }
        }

        .scoreSection {
            background-color: var(--cardBg);

            .mainScoresWrraper {
                background-color: var(--cardBg);
                border: var(--cardBorder);
                color: var(--fontColorWithBg);
                width: 100%;
                border: none;
                display: flex;

                .scoreDetail {
                    display: flex;
                    align-items: center;
                    font-size: 14px;
                    font-weight: 700;
                    text-transform: capitalize;

                    .spanStyle {
                        color: #ffff;
                        background-color: #121212;
                        padding: 6px;
                    }

                    img {
                        background-color: #00b181;
                        padding: 5px;
                    }

                    .tooltipWrraper {
                        padding: 6px;
                        background-color: var(--cardBg);
                        margin-left: 4px;
                        display: flex;

                        .rulesHead {
                            display: none;
                        }

                        &:hover {
                            .rulesHead {
                                display: block;
                                cursor: pointer;
                            }
                        }
                    }
                }

                .scoreNumbersWrraper {
                    padding: 4px;

                    .scoreNumberHeader {
                        display: flex;
                        justify-content: flex-end;
                        width: 90%;
                        background-color: var(--sideBarSelectedTabBg);
                        padding: 4px;

                        span {
                            color: #ffff;
                        }
                    }
                }

                @media screen and (max-width: 994px) {
                    flex-direction: column;
                    display: contents;
                }

                .countriesName {
                    margin-top: 8px;

                    span {
                        font-size: 16px;
                        font-size: 600;
                    }
                }
            }
        }
    }

    .tabWrapper {
        margin-top: 8px;
        margin-left: 8px;

        :global(.ant-tabs) {

            :global(.ant-tabs-content-holder) {
                border-top: 1px solid #dee2e6;
                padding-top: 4px;
            }

            :global(.ant-tabs-nav) {
                margin: 0px;
            }


        }

        :global(.ant-tabs-nav-list) {
            width: 100%;
            background-color: var(--cardBg);
            flex-direction: row;
            justify-content: space-around;


            :global(.ant-tabs-tab) {
                width: 46%;
                display: flex;
                flex-direction: row;
                justify-content: center;
                margin: 0px;

            }

            :global(.ant-tabs-tab-active) {
                background-color: var(--sideBarSelectedTabBg);
                border-radius: 8px;

                :global(.ant-tabs-tab-btn) {
                    color: #ffff;
                }
            }

            :global(.ant-tabs-tab-btn) {
                color: #00b181;
            }

            :global(.ant-tabs-ink-bar-animated) {
                display: none;
            }


        }

        .tvWrraper {
            height: 300px;
            margin-bottom: 80px;
        }

        .betStakesWrraper {
            display: flex;
            justify-content: space-between;
            color: var(--fontColorWithBg);
            background-color: var(--titleBarBg);

            span {
                font-size: 14px;
                font-weight: 600;
                padding: 4px 10px;
                color: var(--fontColorWithBg);

            }
        }

        .tableHeader {
            height: 34px;
            gap: 0px;
            padding-left: 0px;

            h4 {
                padding: 4px;
                font-size: 14px;
                font-size: 400;
            }
        }
    }

    .mainTvWrraper {
        background-color: var(--cardBg);
        width: 100%;
    }

    .tabSize{
        width: 100%;
    }
}

@media screen and (max-width: 770px) {
    .mainTvWrraper {
        width: 70%;
    }
}