@import "@assets/styles/colors";

.loadBalanceContainer {
    border: 1px solid black;
    width: 95%;
    background-color: var(--fontColor);
    margin: auto;
    border-radius: 15px;
    min-height: 400px;
    margin-top: 20px;

    .titlebar {
        border-radius: 12px 12px 0px 0px;
    }

    .formContainer {
        width: 90%;
        margin: auto;
        margin-top: 20px;

        .loadBalanceForm {
            width: 100%;


            .submitBtn {
                float: right;
            }
        }
    }
    
}