@import '@assets/styles/colors';

.usersHeaderBtnContainer {
    width: 100%;

    .btnContainer {
        display: flex;
        flex-wrap: wrap;
        gap: 5px;

        .activeButton {
            border: 1px solid var(--outlinedBtnBorderHover) !important;
            box-shadow: var(--outlinedBtnBoxShadowHover);
            background-color: var(--outlinedBtnBgHover)   
        }

        @media screen and ( max-width: 1080px ) {
            justify-content: center;
        }
      
    }

    @media screen and ( min-width: 1070px )  {
        padding-left: 20px;

    }

}