@import '@assets/styles/colors';

.titleBarContainer {
    height: auto;
    color: var(--fontColorWithBg);
    background-color: var(--titleBarBg);
    padding: 5px 0px 5px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid var(--titleBarBorder);
    
.dangeriousHtmlcolor{
    color: var(--fontColorWithBg);
}
    .titleIconContainer {
        display: flex;  
        flex-direction: row;
        gap: 10px;
        align-items: center;

        h4 {
            font-weight: 600;
            font-size: 20px;
            color: var(--fontColorWithBg);
            margin: 0;
            @media only screen and (max-width:415px){
                font-size: 15px;
                
              }
        }

    }
}