@import "@assets/styles/colors";

.mainWrapper {
   padding: 15px;

   .inputcolor{
    color: #121212;
   }
};
.titleBar{
    width: 100%;
    height: 40px;
    color: var(--fontColorWithBg);
    background-color: var(--titleBarBg);
    padding: 3px 3px 3px 10px ;
    font-size: 16px !important;
    border-radius: 5px;
    margin-top: 5px;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    border-bottom: 1px solid var(--titleBarBorder);
    h4{
        font-size: 16px !important;
        
    }
}
.gapClass{
    gap: 5px;
}
.FlexClass{
    display: flex;
}
.flexAndGapClass{
    display: flex;
    gap: 12px;
}
.titleBarBorder {
    border-radius: 15px 15px 0px 0px; 
    border: var(--cardBorder);
  }
  .colorClass{
    color: var(--fontColorWithBg);
    font-size: 10px;
    font-size: 16px !important;
    
  }
