@import "@assets/styles/colors";

.mainContainer {
  padding-top: 5px;
  padding-left: 5px;

  .container {
    padding: 0px 20px;

    .belockTitleBar {
      max-width: 30%;
      height: 35px;
      justify-content: center;
      @media screen and (max-width: 950px) {
        max-width: 55%;
      }

      h4 {
        font-size: 15px;
        @media screen and (max-width: 950px) {
          font-size: 15px;
        }
        @media screen and (max-width: 475px) {
          font-size: 12px;
        }
      }
    }
    .mainBetLockDiv {
      display: flex;
      background-color: var(--cardBg);
      padding-left: 35px;
      gap: 10px;
      width: 30%;
      padding-bottom: 20px;
      flex-direction: column;
      @media screen and (max-width: 950px) {
        width: 55%;
      }
      .allCasinoText {
        font-size: 18px;
        font-weight: bold;
      }
      .checkBoxGroup {
        display: flex;
        flex-direction: column;
        align-items: baseline;
        .ant-checkbox-inner {
          border: 2px solid;
        }
      }

      .childCheckbox {
        margin-left: 15px;
      }
    }
    .betLocksFooter {
      margin-top: 20px;
      border-radius: 0;
      @media screen and (max-width: 350px) {
        min-width: 100%;
        margin: auto;
        justify-content: center;
        align-items: center;
        display: flex;
        margin-top: 10px;
      }
    }
  }
}
