.mainContainer {
  padding: 15px;
  padding-bottom: 0px;
  min-width: 100%;

  .container {
    border: 2px solid;
    min-width: 100%;
    display: flex;
  }

  .finalSheetTitleBar {
    border-radius: 2px;
  }
  .finalSheetBarDiv {
    height: 80px;
    display: flex;
    flex-direction: column;
    background-color: var(--titleBarBg);
    margin-top: 10px;
    padding-top: 10px;
    border: var(--cardBorder);
    gap: 10px;
    padding-left: 40px;
  }

  .finalSheetBar {
    display: flex;
    flex-direction: row;
    gap: 10px;
    font-size: 20px;
    align-items: center;
    .finalSheetLable {
      color: var(--fontColorWithBg);
      font-weight: 600;
      font-size: 20px;
      @media screen and (max-width: 424px){
        font-size: 18px;
        
        
      }

    }
  }
  .finalSheetCheckBoxDiv {
    display: flex;
    gap: 10px;
    align-items: center;
    .finalSheetCheckBoxLable {
      color: var(--fontColorWithBg);
    }
  }
  .mainTableDiv {
    display: flex;
    padding: 10px;
    flex-direction: row;
    gap: 10px;
    margin-top: 10px;

    .gridOne {
      min-width: 33%;

      .positiveClient {
        :global(.ant-table-footer) {
          padding: 0;
          border-radius: 0;
          color: var(--fontColorWithBg);
          border: var(--cardBorder);
          background-color: var(--titleBarBg);

          .footerTotal {
            display: flex;
            flex-direction: row;
            color: var(--fontColorWithBg);
            justify-content: space-between;
            padding-inline: 8px;
          }
        }
      }
      @media screen and (max-width: 966px) {
        min-width: 60%;
      }
      @media screen and (max-width: 476px){
        min-width: 70%;
        justify-content: center;
      }
    }
    .gridTwo {
      min-width: 33%;

      .negativeClient {
        :global(.ant-table-footer) {
          padding: 0;
          border-radius: 0;
          color: var(--fontColorWithBg);
          border: var(--cardBorder);
          background-color: var(--titleBarBg);

          .footerTotal {
            display: flex;
            flex-direction: row;
            color: var(--fontColorWithBg);
            justify-content: space-between;
            padding-inline: 8px;
          }
        }
      }
      @media screen and (max-width: 966px) {
        min-width: 60%;
      }
      @media screen and (max-width: 476px){
        min-width: 70%;
        justify-content: center;
      }
    }
    .gridThree {
      min-width: 33%;
    }
    @media screen and (max-width: 966px) {

      flex-direction: column;
      align-items: center;
    }
  }
}
