.footerStyle {
  min-width: 100%;
  border-radius: 0 0 10px 0;
  background-color: var(--cardBg);
  text-align: center;
  margin-top: 10px;
  text-decoration-color: none;

  @media screen and (max-width: 768px) {
    font-size: 9px;
  }
  @media screen and (max-width: 350px) {
    font-size: 9px;
  }

  .footerText {
    a {
      font-size: 11px;
      color: var(--footer);
    }
  }
}
