.adminDashboardWrraper {
    padding: 14px;

    .searchBarButtunCard {
        background-color: var(--cardBg);
        margin-bottom: 20px;
        border: var(--cardBorder);
        width: 100%;

        .searchBarCol {
            display: flex;
            .searchIcon{
                display: flex;
                justify-content: center;
                align-items: center;
                img{
                    width: 16px;
                    height: 16px;
                    margin-right: 7px;
                }
            }

            .searchBarButton {
                margin: 20px;
                align-items: inside;

                :global(.ant-input-search-button) {
                    background-color: var(--filledBtnBg);
                }
            }
        }
    }

    .titelBarWrraper {
        width: 100%;
        height: 54px;
        color: var(--fontColorWithBg);
        background-color: var(--titleBarBg);
        padding-left: 40px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid var(--titleBarBorder);

        h3 {
            margin-right: 6px;
        }
    }
    .tableCard{
        background-color: var(--cardBg);
        margin-bottom: 20px;
        border: var(--cardBorder);
        width: 100%;  
        .tableWrraper{
            margin: 10px;
        }
    }
}