@import "@assets/styles/colors";

.mainCashWrapper {
    padding-bottom: 40px;
    background-color: #e4e5e6;
.backgroundColorChange{
    background-color: var(--pagesBg);
    border: var(--cardBorder);
    padding: 7px 20px;
    .depositeTitleBar{
        background-color: #00b181;
    }
    .withdrawTitleBar{
        background-color: #dc3545;
    }
    .withdrawBtn{
        background-color: #dc3545;
    }

}
    .cashContainer {
        background-color: var(--cardBg);
        border: var(--cardBorder);
        padding-bottom: 20px;
        padding: 0px 20px;

        .btnWrapper {
            display: flex;
        }

        ;

        .tabWrapper {
            margin-top: 14px;

            :global(.ant-tabs) {

                :global(.ant-tabs-content-holder) {
                    border-top: 1px solid #dee2e6;
                    padding-top: 4px;
                }
            }

            :global(.ant-tabs-nav-list) {
                width: 100%;
                background-color: var(--cardBg);
                flex-direction: row;
                justify-content: space-around;


                :global(.ant-tabs-tab) {
                    width: 46%;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    margin: 0px;

                }

                :global(.ant-tabs-tab-active) {
                    background-color: var(--sideBarSelectedTabBg);
                    border-radius: 8px;

                    :global(.ant-tabs-tab-btn) {
                        color: #ffff;
                    }
                }

                :global(.ant-tabs-tab-btn) {
                    color: #00b181;
                }

                :global(.ant-tabs-ink-bar-animated){
                    display: none;
                }


            }

        }

    }

    .depositeCashWrapper {
        margin-left: 20px;
        margin-top: 20px;

        .submitBtn {
            border-top: 1px solid rgba(0, 0, 0, .125);
            background-color: rgba(0, 0, 0, .03);
            padding: 8px;
        }

        ;
    }

    ;

    .depositeCashTable {
        background-color: var(--cardBg);
        border: var(--cardBorder);
        width: 100%;

        .depositForm {
            padding: 10px;

            .labelAmount {
                // position: absolute;
                height: 47px;
                display: flex;
                align-items: center;
                border-bottom-left-radius: 8px;
                border-top-left-radius: 8px;
                background: var(--cardIconBg);
                border: 1px solid #ced4da;
                padding: 14px;
                color: var(--fontColor)
            };
        };

        .depositeCashForm {
            :global(.ant-form-item-label) {
                display: flex;
            }
        }
    }

}