@import "@assets/styles/colors";

.mainWrapper {
   padding: 15px;

   .inputcolor{
    color: #121212;
   }
};

:global(.ant-picker-dropdown){
    :global(.ant-picker-panel-container){
        // border: 5px solid blue;
        overflow: scroll;
        max-width: 300px;
        max-height: 300px;
    }
   
    .ant-picker .ant-picker-clear
    .anticon{
        color: red;
    }
   }
   :global(.ant-picker){
    :global(.ant-picker-input){
       color: #121212 !important;
       &:hover{
        color: #121212 !important;
       }
    }
   }
   :global(.ant-picker-clear){
    background: #121212 !important; 
}
   .titleBar{
    width: 100%;
    height: 40px;
    color: var(--fontColorWithBg);
    background-color: var(--titleBarBg);
    padding: 3px 3px 3px 10px ;
    font-size: 16px !important;
    border-radius: 5px;
    margin-top: 5px;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    border-bottom: 1px solid var(--titleBarBorder);
    h4{
        font-size: 16px !important;
        
    }
}
.FlexClass{
    display: flex;
}
.paddingLeftClass{
    padding-left: 20px;

}
.gapClass{
    gap: 5px;
}
.flexAndGapClass{
    display: flex;
    gap: 12px;
}
.FlexClass{
    display: flex;
}
.colorClass{
    color: var(--fontColorWithBg);
    font-size: 10px;
    font-size: 16px !important;
    
  }
   .titlebar {
    background-color: var(--titleBarBg);
    border-radius: 5px;
}
